import type { TrainingPlan } from '@/types/database'

import type { PlanFormData } from './PlanForm'

export function planToFormData(plan: TrainingPlan): PlanFormData {
  return {
    name: plan.name,
    unitName: plan.unit_name,
    unitAmount: plan.unit_amount,
    totalAmount: plan.total_amount,
    scheduleType: plan.schedule_type,
    scheduleDays: plan.schedule_days ?? ['mon', 'tue', 'wed', 'thu', 'fri'],
    startDate: plan.start_date,
  }
}

export function formDataToPlanInsert(data: PlanFormData, userId: string) {
  return {
    user_id: userId,
    name: data.name,
    unit_name: data.unitName,
    unit_amount: data.unitAmount,
    total_amount: data.totalAmount,
    schedule_type: data.scheduleType,
    schedule_days: data.scheduleType === 'weekly' ? data.scheduleDays : null,
    start_date: data.startDate,
  }
}

export function formDataToPlanUpdate(data: PlanFormData) {
  return {
    name: data.name,
    unit_name: data.unitName,
    unit_amount: data.unitAmount,
    total_amount: data.totalAmount,
    schedule_type: data.scheduleType,
    schedule_days: data.scheduleType === 'weekly' ? data.scheduleDays : null,
    start_date: data.startDate,
  }
}
