import { Suspense, useMemo, useState } from 'react'

import { useAtomValue } from 'jotai'

import FolderIcon from '@mui/icons-material/Folder'
import {
  Autocomplete,
  Box,
  Breadcrumbs,
  Button,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Link,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  TextField,
  Typography,
} from '@mui/material'

import NumberSpinner from '@/components/NumberSpinner'
import { flashManConfigAtom } from '@/pages/flash-man/atoms'
import { useRepositoryList } from '@/pages/flash-man/hooks'
import { useFmCount, useGetDirectories } from '@/pages/flash-man/hooks/useGetContent'
import type { ContentItem, RepoIdentifier } from '@/pages/flash-man/types'

export type FlashManFolderResult = {
  owner: string
  repo: string
  path: string
  fileCount: number
}

type FlashManFolderDialogProps = {
  open: boolean
  onClose: () => void
  onSelect: (result: FlashManFolderResult) => void
}

function DialogInner({ onClose, onSelect }: Omit<FlashManFolderDialogProps, 'open'>) {
  const config = useAtomValue(flashManConfigAtom)
  const repos = useRepositoryList(config.repos)

  const [selectedRepo, setSelectedRepo] = useState<RepoIdentifier>()
  const [currentPath, setCurrentPath] = useState('')
  const [repeatCount, setRepeatCount] = useState(1)

  const contentIdentifier = useMemo(() => {
    const owner = selectedRepo?.owner ?? ''
    const repo = selectedRepo?.repo ?? ''
    return {
      owner,
      repo,
      path: currentPath,
    }
  }, [currentPath, selectedRepo])

  const { isLoading, data: directories } = useGetDirectories(contentIdentifier)
  const { isLoading: isLoadingCount, data: fmFileCount = 0 } = useFmCount(contentIdentifier)

  function handleRepoChange(value?: { owner: string; repo: string }) {
    setSelectedRepo(value)
    setCurrentPath('')
  }

  function handleDirectoryClick(dir: ContentItem) {
    if (!selectedRepo) return
    setCurrentPath(dir.path)
  }

  function handleBreadcrumbClick(index: number) {
    if (!selectedRepo) return
    const parts = currentPath.split('/')
    const newPath = parts.slice(0, index + 1).join('/')
    setCurrentPath(newPath)
  }

  function handleBreadcrumbRoot() {
    if (!selectedRepo) return
    setCurrentPath('')
  }

  function handleSelect() {
    if (!selectedRepo || fmFileCount === null) return
    onSelect({
      owner: selectedRepo.owner,
      repo: selectedRepo.repo,
      path: currentPath,
      fileCount: fmFileCount * repeatCount,
    })
  }

  const pathParts = currentPath ? currentPath.split('/') : []
  const currentRepoValue = selectedRepo
    ? repos.find((r) => r.owner === selectedRepo.owner && r.repo === selectedRepo.repo) || null
    : null

  // console.log('DialogInner', { selectedRepo, directories, fmFileCount })
  return (
    <>
      <DialogTitle>Flash-Man 폴더 선택</DialogTitle>
      <DialogContent>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1, position: 'relative' }}>
          <Autocomplete
            fullWidth
            options={repos}
            getOptionLabel={(option) => option.label}
            value={currentRepoValue}
            onChange={(_, newValue) => {
              handleRepoChange(
                newValue ? { owner: newValue.owner, repo: newValue.repo } : undefined
              )
            }}
            renderInput={(params) => <TextField {...params} label="레포지터리" />}
          />

          {selectedRepo && (
            <>
              <Breadcrumbs>
                <Link
                  component="button"
                  underline="hover"
                  onClick={handleBreadcrumbRoot}
                  color={currentPath ? 'inherit' : 'text.primary'}
                >
                  {selectedRepo.repo}
                </Link>
                {pathParts.map((part, index) => (
                  <Link
                    key={index}
                    component="button"
                    underline="hover"
                    onClick={() => handleBreadcrumbClick(index)}
                    color={index === pathParts.length - 1 ? 'text.primary' : 'inherit'}
                  >
                    {part}
                  </Link>
                ))}
              </Breadcrumbs>

              {isLoading ? (
                <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
                  <CircularProgress />
                </Box>
              ) : directories.length > 0 ? (
                <List dense>
                  {directories.map((dir) => (
                    <ListItemButton key={dir.path} onClick={() => handleDirectoryClick(dir)}>
                      <ListItemIcon>
                        <FolderIcon />
                      </ListItemIcon>
                      <ListItemText primary={dir.name} />
                    </ListItemButton>
                  ))}
                </List>
              ) : (
                <Typography variant="body2" color="text.secondary" sx={{ py: 2 }}>
                  하위 폴더가 없습니다.
                </Typography>
              )}

              {/* File count & repeat settings */}
              <Box
                sx={({ palette }) => ({
                  position: 'sticky',
                  bottom: '-24px',
                  display: 'flex',
                  flexDirection: 'row',
                  alignItems: 'flex-end',
                  // backdropFilter: 'blur(8px)',
                  backgroundColor: palette.background.paper,
                  gap: 2,
                })}
              >
                <Box>
                  <NumberSpinner
                    size="small"
                    label="반복횟수"
                    min={1}
                    value={repeatCount}
                    onValueChange={(value) => {
                      if (value) {
                        setRepeatCount(value)
                      }
                    }}
                  />
                </Box>
                <Box>
                  <Typography variant="body2" color="text.secondary" sx={{ whiteSpace: 'nowrap' }}>
                    파일 수: {isLoadingCount ? '확인 중...' : (fmFileCount ?? '-')}
                  </Typography>

                  <Typography variant="body2" sx={{ whiteSpace: 'nowrap' }}>
                    전체 훈련량: <strong>{fmFileCount ? fmFileCount * repeatCount : '-'}</strong>
                  </Typography>
                </Box>
              </Box>
            </>
          )}
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>취소</Button>
        <Button
          variant="contained"
          onClick={handleSelect}
          disabled={!selectedRepo || fmFileCount === 0}
        >
          선택
        </Button>
      </DialogActions>
    </>
  )
}

export function FlashManFolderDialog({ open, onClose, onSelect }: FlashManFolderDialogProps) {
  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <Suspense
        fallback={
          <Box sx={{ display: 'flex', justifyContent: 'center', py: 8 }}>
            <CircularProgress />
          </Box>
        }
      >
        {open && <DialogInner onClose={onClose} onSelect={onSelect} />}
      </Suspense>
    </Dialog>
  )
}
