import { useMemo } from 'react'

import { useQuery } from '@tanstack/react-query'

import { useOctokit } from '../hooks'
import { countFlashManFiles, getContents } from '../lib/github'
import type { ContentIdentifier } from '../types'

export function useGetContent(contentIdentifier?: ContentIdentifier) {
  const owner = contentIdentifier?.owner ?? ''
  const repo = contentIdentifier?.repo ?? ''
  const path = contentIdentifier?.path ?? ''
  const octokit = useOctokit()
  return useQuery({
    queryKey: ['useGetContent', owner, repo, path],
    queryFn: () => {
      if (owner && repo) {
        return getContents(octokit, { owner, repo }, path)
      }
    },
  })
}

export function useGetDirectories(contentIdentifier?: ContentIdentifier) {
  const { data: dataOrig, ...rest } = useGetContent(contentIdentifier)
  const data = useMemo(() => {
    if (Array.isArray(dataOrig)) {
      return dataOrig.filter((item) => item.type === 'dir')
    }
    return []
  }, [dataOrig])

  return {
    ...rest,
    data,
  }
}

export function useFmCount(contentIdentifier?: ContentIdentifier) {
  const owner = contentIdentifier?.owner ?? ''
  const repo = contentIdentifier?.repo ?? ''
  const path = contentIdentifier?.path ?? ''
  const octokit = useOctokit()
  return useQuery({
    queryKey: ['useFmCount', owner, repo, path],
    queryFn: () => {
      if (owner && repo) {
        // console.log('useFmCount', { owner, repo, path })
        return countFlashManFiles(octokit, { owner, repo }, path)
      }
    },
  })
}
