import { useState } from 'react'

import FolderOpenIcon from '@mui/icons-material/FolderOpen'
import {
  Box,
  Typography,
  TextField,
  Button,
  FormControl,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
  ToggleButton,
  ToggleButtonGroup,
  Paper,
  Stack,
  Alert,
  IconButton,
} from '@mui/material'

import NumberSpinner from '@/components/NumberSpinner'
import type { ScheduleType, WeekDay } from '@/types/database'
import {
  ALL_WEEKDAYS,
  getWeekDayLabel,
  calculateTotalSessions,
  calculateEndDate,
  formatDate,
} from '@/utils/schedule'

import { FlashManFolderDialog, type FlashManFolderResult } from './FlashManFolderDialog'

export interface PlanFormData {
  name: string
  unitName: string
  unitAmount: number
  totalAmount: number
  scheduleType: ScheduleType
  scheduleDays: WeekDay[]
  startDate: string
}

interface PlanFormProps {
  initialData?: Partial<PlanFormData>
  onSubmit: (data: PlanFormData) => Promise<void>
  onCancel: () => void
  loading: boolean
  error: string | null
  submitLabel: string
  loadingLabel: string
  title: string
}

export function PlanForm({
  initialData,
  onSubmit,
  onCancel,
  loading,
  error,
  submitLabel,
  loadingLabel,
  title,
}: PlanFormProps) {
  const [fmDialogOpen, setFmDialogOpen] = useState(false)

  function handleFmSelect(result: FlashManFolderResult) {
    const unitName = `fm://${result.owner}/${result.repo}${result.path ? '/' + result.path : ''}`
    setFormData((prev) => ({
      ...prev,
      unitName,
      totalAmount: result.fileCount,
    }))
    setFmDialogOpen(false)
  }

  const [formData, setFormData] = useState<PlanFormData>({
    name: initialData?.name ?? '',
    unitName: initialData?.unitName ?? '',
    unitAmount: initialData?.unitAmount ?? 1,
    totalAmount: initialData?.totalAmount ?? 100,
    scheduleType: initialData?.scheduleType ?? 'daily',
    scheduleDays: initialData?.scheduleDays ?? ['mon', 'tue', 'wed', 'thu', 'fri'],
    startDate: initialData?.startDate ?? new Date().toISOString().split('T')[0],
  })

  const previewPlan = {
    name: formData.name,
    unit_name: formData.unitName,
    unit_amount: formData.unitAmount,
    total_amount: formData.totalAmount,
    schedule_type: formData.scheduleType,
    schedule_days: formData.scheduleType === 'weekly' ? formData.scheduleDays : null,
    start_date: formData.startDate,
  }

  const totalSessions = calculateTotalSessions({
    ...previewPlan,
    id: 0,
    user_id: '',
    created_at: '',
    updated_at: '',
  })
  const endDate = calculateEndDate({
    ...previewPlan,
    id: 0,
    user_id: '',
    created_at: '',
    updated_at: '',
  })

  async function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault()
    await onSubmit(formData)
  }

  function handleScheduleDaysChange(_: React.MouseEvent<HTMLElement>, newDays: WeekDay[]) {
    setFormData((prev) => ({ ...prev, scheduleDays: newDays }))
  }

  return (
    <Box component="form" onSubmit={handleSubmit}>
      <Typography variant="h5" gutterBottom>
        {title}
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      <Stack spacing={3}>
        <TextField
          label="계획 이름"
          value={formData.name}
          onChange={(e) => setFormData((prev) => ({ ...prev, name: e.target.value }))}
          placeholder="예: 알고리즘 문제풀이"
          fullWidth
          required
        />

        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <TextField
            label="훈련 단위"
            value={formData.unitName}
            onChange={(e) => setFormData((prev) => ({ ...prev, unitName: e.target.value }))}
            placeholder="예: 문제, 페이지, 강의"
            fullWidth
            required
          />
          <IconButton onClick={() => setFmDialogOpen(true)} title="Flash-Man 폴더 선택">
            <FolderOpenIcon />
          </IconButton>
        </Box>

        <NumberSpinner
          style={{ marginTop: '1rem' }}
          label="1회당 훈련량"
          min={1}
          value={formData.unitAmount}
          onValueChange={(value) => {
            if (value) {
              setFormData((prev) => ({ ...prev, unitAmount: value }))
            }
          }}
        />
        <NumberSpinner
          label="전체 훈련량"
          min={1}
          value={formData.totalAmount}
          onValueChange={(value) => {
            if (value) {
              setFormData((prev) => ({ ...prev, totalAmount: value }))
            }
          }}
        />

        <TextField
          label="시작일"
          type="date"
          value={formData.startDate}
          onChange={(e) => setFormData((prev) => ({ ...prev, startDate: e.target.value }))}
          fullWidth
        />

        <FormControl>
          <FormLabel>훈련 주기</FormLabel>
          <RadioGroup
            row
            value={formData.scheduleType}
            onChange={(e) =>
              setFormData((prev) => ({ ...prev, scheduleType: e.target.value as ScheduleType }))
            }
          >
            <FormControlLabel value="daily" control={<Radio />} label="매일" />
            <FormControlLabel value="weekly" control={<Radio />} label="주간 (요일 선택)" />
          </RadioGroup>
        </FormControl>

        {formData.scheduleType === 'weekly' && (
          <Box>
            <FormLabel sx={{ mb: 1, display: 'block' }}>훈련 요일</FormLabel>
            <ToggleButtonGroup
              value={formData.scheduleDays}
              onChange={handleScheduleDaysChange}
              aria-label="훈련 요일"
            >
              {ALL_WEEKDAYS.map((day) => (
                <ToggleButton key={day} value={day}>
                  {getWeekDayLabel(day)}
                </ToggleButton>
              ))}
            </ToggleButtonGroup>
          </Box>
        )}

        <Paper
          sx={{
            p: 2,
            bgcolor: (theme) => (theme.palette.mode === 'dark' ? 'grey.900' : 'grey.50'),
          }}
        >
          <Typography variant="subtitle2" color="text.secondary" gutterBottom>
            예상 훈련 정보
          </Typography>
          <Typography variant="body2">
            총 훈련 횟수: <strong>{totalSessions}회</strong>
          </Typography>
          <Typography variant="body2">
            예상 종료일: <strong>{formatDate(endDate)}</strong>
          </Typography>
        </Paper>

        <Stack direction="row" spacing={2}>
          <Button variant="outlined" onClick={onCancel} disabled={loading}>
            취소
          </Button>
          <Button type="submit" variant="contained" disabled={loading}>
            {loading ? loadingLabel : submitLabel}
          </Button>
        </Stack>
      </Stack>

      <FlashManFolderDialog
        open={fmDialogOpen}
        onClose={() => setFmDialogOpen(false)}
        onSelect={handleFmSelect}
      />
    </Box>
  )
}
